import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			imgFile: '',
			nickName: '',
			sexValue: '',
			sexList: [{label: '男',value: 1},{label: '女',value: 2}],
			heightList: [],
			heightValue: '',
			birthValue: '',
			defaultValBirth: ['1990','06','15'],
			workList: [{label: '',value: ''},{label: '',value: ''}],
			workValue: '',
			regionValue: ''
		}
	},
	onLoad() {
		// 身高选择列表
		for(let i=120; i<=200; i++) {
			let item = {
				label: i + ' cm',
				value: i
			}
			this.heightList.push(item)
		}
		// 职业选择列表
		this.$http.get({
			url: '/v1/rest/public/findDictList',
			data:{
				skey: 'occupation',
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.workList = resp.data
			}
		})
	},
	methods: {
		choiceImg() {
			const that = this
			// uni.chooseImage({
			// 	count: 6, //默认9
			// 	sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			// 	success: function (res) {
			// 		console.log(res)
			// 		that.imgFile = res.tempFilePaths
			// 	}
			// });
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				success: function(res) {
						// 预览图片
						uni.previewImage({
								urls: res.tempFilePaths,
								success: function(resp) {
									console.log(resp)
								}
								// longPressActions: {
								// 		success: function(data) {
								// 				console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
								// 		},
								// 		fail: function(err) {
								// 				console.log(err.errMsg);
								// 		}
								// }
						});
				}
			});
		},
		InputNickName(v) {
			this.nickName = v.detail
		},
		selectSex() {
			this.$refs.picker1.show()
		},
		confirmSex(v) {
			this.sexValue = v.result
		},
		// 选择身高
		selectHeight(v) {
			this.$refs.picker2.show()
		},
		confirmHeight(v) {
			this.heightValue = v.result
		},
		// 选择生日
		selectBirth(v) {
			this.$refs.picker3.show()
		},
		confirmBirth(v) {
			this.birthValue = v.result
		},
		// 选择职业
		selectWork(v) {
			this.$refs.picker4.show()
		},
		confirmWork(v) {
			this.workValue = v.result
		},
		// 选择地区
		selectRegion(v) {
			this.$refs.picker5.show()
		},
		confirmRegion(v) {
			this.regionValue = v.result
		},
	}
}
